/* Блоки */
.container {
  max-width: 1300px;
  margin: 0 auto; }

.page-header__top {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  min-height: 90px; }

.page-header__tel {
  position: relative;
  margin-left: auto;
  font-size: 24px;
  line-height: 24px;
  color: #53565A;
  font-weight: bold;
  font-style: normal; }

.page-header__tel::after {
  content: '';
  position: absolute;
  right: -35px;
  top: -5px;
  width: 30px;
  height: 30px;
  background-image: url("../img/icon-tel.svg");
  background-repeat: no-repeat; }

.page-header__bottom {
  background-image: url(../img/bg-header.jpg);
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center;
  background-color: #B7B2AF; }
  @media (max-width: 1440px) {
    .page-header__bottom {
      background-size: cover; } }

.page-header__bottom-wrapper {
  min-height: 230px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start; }

.page-header__city {
  font-size: 35px;
  line-height: 25px;
  font-weight: normal;
  font-style: normal;
  color: #53565A;
  background-color: white; }

.page-header__room {
  font-size: 45px;
  line-height: 45px;
  font-weight: bold;
  font-style: normal;
  color: #000000;
  background-color: white; }

.page-header__city,
.page-header__room {
  margin: -1px;
  padding: 10px 15px; }

.main-nav {
  display: flex;
  justify-content: space-between; }

.main-nav__toggle {
  display: none; }

.main-nav__list {
  display: flex;
  justify-content: space-between; }

.site-list {
  margin: 0;
  padding: 0;
  list-style: none;
  margin-left: 10px; }

.site-list__item {
  margin-left: 50px; }

.site-list__link {
  font-size: 16px;
  line-height: 14px;
  color: #FFC000;
  font-weight: normal;
  font-style: normal; }
